*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

p{
    margin-bottom: 0;
}

.onedesign{
    width: 100%;
    min-height: 100vh;
    background-color: black;
    padding: 10px;
    height: 100vh;
}

.onecontainer{
    border-radius: 5px;
    width: 100%;
    max-width: 700px;
    margin: 100px auto 20px;
    text-align: center;
}

.onetop{
    color: #ffffff;
    background: #13929c;
}

.onetop h1{
    font-size: 35px;
    padding: 5px;
}

.onetop p{
    font-size: 18px;
    padding-bottom: 30px;
}

.onetop img{
    margin-right: 25px;
    height: 50px;
    width: 160px;
    padding-bottom: 10px;
}

.onemain{
    color : #ffffff;
    background:#0a5783;
    padding: 20px;
}

.oneheading p{
    padding-top: 10px;
    font-weight: 500;
    font-size: 23px;
}

.oneabout{
    padding: 20px;
    display: flex;
    justify-content: center;
    
    align-items: center;
}

.oneaboutImage img{
    height: 60px;
    width: 60px;
    margin: 10px;
    transform: translateY(-30px);
}

.onetext{
    max-width: 300px;
    text-align: left;
}

.onetext h6{
    margin: 0;
    font-size: 15px;
}

.onetext span{
    margin: 0;
    font-size: 12px;
    line-height: 12px;
}

.onemainImage img{
    height: 200px;
    width: 200px;
}

.onetext p{
    margin-top: 2px;
    font-size: 11px;
}

.onefooter {
    color: #ffffff;
    background: #1aa6a6;
    display: flex;
    text-align: center;
    justify-content: center;
    padding: 10px;
}

.onefooterText{
    font-size: 15px;
    color:#d4efee;
    font-weight: 700;
    text-align: left;
    padding-right: 20px;
    border-right: 3px solid #ffffff;
}

.oneline{
    border-top: 1px solid black;
    margin: 10px 0;
}

.onefooter img{
    width: 500px;
    height: 50px;
    padding-left: 10px;
    padding-top: 10px;
}